.footer {
  @apply relative bg-gradient-to-b from-[#38b2ff] to-[#0073d6] text-white  before:bg-[url('/public/images/footer-decor-left.svg')] mobile:before:bg-[url('/public/images/footer-decor-left-m.svg')] before:w-[632px] mobile:before:w-[344px] before:h-[403px] mobile:before:h-[278px] before:absolute before:bottom-0  before:left-0 after:bg-[url('/public/images/footer-decor-right.svg')] mobile:after:bg-[url('/public/images/footer-decor-right-m.svg')] after:w-[509px] mobile:after:w-[180px] after:h-[445px] mobile:after:h-[144px] after:absolute after:top-0  after:right-0;

  .below {
    @apply relative flex justify-between max-w-[1132px] mx-auto -mt-14 mobile:-mt-7 pb-10 px-4 z-10;
  }
}

.contacts {
  @apply max-w-[260px] mobile:max-w-none mobile:mx-auto mobile:mb-7;
  li {
    @apply flex w-[260px] mobile:w-[320px] bg-white rounded-lg p-2 pl-3 text-[#1E282D] first:rounded-br-none last:rounded-tr-none;

    & + li {
      @apply mt-4;
    }
  }

  svg {
    @apply min-w-[24px] block;
  }

  h4 {
    @apply font-semibold;
  }

  a {
    @apply text-xs;
  }
}

.author {
  @apply text-[0] flex justify-between mt-auto;

  a {
    @apply opacity-50 transition-opacity duration-300 hover:opacity-100;
  }

  .line {
    @apply w-[2px] h-5 bg-[rgba(255,255,255,0.5)] rounded-full mx-3;
  }
}
