.google-play,
.ios {
  will-change: filter;
  filter: drop-shadow(0px 4px 25px rgba(13, 149, 255, 0.15));

  &:hover {
    .fill-white {
      fill: black;
    }

    .fill-black {
      fill: white;
    }
  }

  .fill-white,
  .fill-black {
    transition: fill 0.3s ease;
  }
}

@keyframes s {
  0% {
    fill: 'white';
  }
  50% {
    fill: 'black';
  }
  100% {
    fill: 'white';
  }
}

#get-cash {
  .google-play,
  .ios {
    &:hover {
      .bg {
        fill: black;
      }

      .fill-white {
        fill: white;
      }
    }

    .bg {
      fill: white;
    }

    .fill-white {
      fill: black;
    }
  }

  .google-play {
    width: 175px;
    height: 52px;
  }

  .ios {
    width: 156px;
    height: 52px;
  }

  .fill-white,
  .fill-black {
    animation: none;
  }
}

@media (min-width: 768px) {
  .google-play,
  .ios {
    .fill-white,
    .fill-black {
      animation: none;
    }
  }
}
