.container {
  @apply mx-auto flex justify-between max-w-layout pt-7 relative overflow-hidden;
}

.header {
  @apply relative h-0 z-20;
}

.nav {
  @apply list-none;

  li {
    @apply text-white mr-10 last:mr-0 inline-block text-sm font-semibold;
  }
}

@media (max-width: 767px) {
  .responsive {
    @apply flex flex-col overflow-hidden fixed inset-0 z-[11] bg-gradient pt-24 pb-10 before:bg-[url('/public/images/nav-decor-top.svg')] before:w-[232px] before:h-[198px] before:absolute before:top-0 before:right-0 before:z-[-1] after:bg-[url('/public/images/nav-decor-bottom.svg')] after:w-[344px] after:h-[237px] after:absolute after:bottom-0 after:z-[-1] after:left-0;
  }

  .link {
    @apply mx-auto;
  }

  .logo {
    @apply w-[70px] h-[16px] block;
  }

  .nav li {
    @apply w-full text-xl text-center mb-4;
  }

  .info {
    @apply text-white text-xs text-center mt-auto max-w-[220px] mx-auto relative;
  }

  .info li:not(:last-child) {
    @apply mt-2;
  }
}

@media (max-width: 1100px) {
  .header {
    @apply px-4;
  }
}
